import {Tab, Row, Col, Nav, InputGroup, FormControl} from 'react-bootstrap';
import styled, {ThemeContext} from "styled-components";
import paginationFactory, {PaginationProvider} from "react-bootstrap-table2-paginator";
import React, {useContext, useEffect, useMemo, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import BootstrapTable from 'react-bootstrap-table-next';
import {withRouter} from "react-router-dom";

import SearchIcon from "../../assets/images/search.svg";
import Loading from "../../components/Loading";
import CurrencyText from "../../components/CurrencyText";
import {fetchAllCoins, fetchCoinMarketPrices, fetchMarketCoins} from "../../state/market/actions";
import CurrencyLogo from "../../components/CurrencyLogo";
import ArrowDown from "../../components/Icons/ArrowDown";
import ResponsiveTable from "../../components/ResponsiveTable";

const LogoContainer = styled.div`
  width: 32px;
  height: 32px;
  border-radius: 32px;
  
  
  @media (max-width: 991px) {
    width: 24px;
    height: 24px;
    border-radius: 24px;
  }
`

const Logo = styled.img`
  width: 32px;
  height: 32px;
  border-radius: 32px;
  background-color: white;
  border: 2px solid white;
  
  
  @media (max-width: 991px) {
    width: 24px;
    height: 24px;
    border-radius: 24px;
  }
`

const CustomNav = styled(Nav)`
  margin-left: -30px !important;
  margin-right: -30px !important;
  overflow: auto;
  
  @media (min-width: 768px) {
    margin-left: -10px !important;
    margin-right: -10px !important;
  }
`

const CustomNavItem = styled(Nav.Item)`
  flex-grow: initial !important;
  
  padding: 0 10px 10px;
  
  
    @media (max-width: 767px) {
      padding: 0 5px 10px;
    }
  
  &:first-child {
    @media (max-width: 767px) {
      padding-left: 30px;
    }
  }
  &:last-child {
    @media (max-width: 767px) {
      padding-right: 30px;
    }
  }
`
const CustomNavLink = styled(Nav.Link)`
  border-radius: 18px !important;
  color: ${({ theme }) => theme.primary};
  background-color: rgba(71, 183, 254, 0.15);
  white-space: nowrap;
  padding: 14px 24px;
  min-height: 56px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  
  
    @media (max-width: 767px) {
      padding: 6px 15px;
      font-size: 1rem;
      min-height: 32px;
      border-radius: 12px !important;
    }
  
  &:hover {
    color: ${({ theme }) => theme.primary};
  }
  
  &.active {
    color: white;
    background-color: ${({ theme }) => theme.primary};
  }
`

const HeaderCol = styled(Col)`
  margin: -10px 0 20px;
  
  @media (min-width: 768px) {
    margin-bottom: 25px;
  }
`

const CustomInputGroup = styled(InputGroup)`
  margin-bottom: 30px;
`


const MarketLink = styled.a`
  @media(max-width: 991px) {
    flex-basis: 100px;
  }
`

const CustomTitle = styled.h4`
  color: ${({theme}) => theme.text1}
  font-size: 1.25rem;
  
  @media (max-width: 991px) {
    font-size: 0.875rem;
  }
`


const SymbolText = styled.span`
  font-weight: 500;
  font-size: 1rem;
  
  @media (max-width: 991px) {
    font-size: 0.875rem;
    font-weight: 400;
  }
`

const CellText = styled.span`
  font-weight: 500;
  font-size: .875rem;
  
  &.font-size-base {
    font-size: 1rem;
  }
  
  @media (max-width: 991px) {
    font-weight: 700;
    
    &.label {
    font-weight: 500;
    }
  }
`

const MarketTokens = props => {
    const [query, setQuery] = useState('');
    const [expanded, setExpanded] = useState([]);
    const [page, setPage] = useState(1);
    const [pageSize, setPageSize] = useState(50);
    const dispatch = useDispatch();
    const marketCoins = useSelector(state => state.market.marketCoins);
    const allTokens = useSelector(state => state.market.allTokens);
    const theme = useContext(ThemeContext);

    const allTokensData = useMemo(() => {
        const filterText = query.trim().toLowerCase();
        if(filterText.length > 0) {
            return allTokens.data.filter(token => token.name.toLowerCase().indexOf(filterText) > -1 || token.symbol.toLowerCase().indexOf(filterText) > -1)
        } else {
            return allTokens.data
        }
    }, [allTokens, query])

    const marketCoinsData = useMemo(() => {
        const filterText = query.trim().toLowerCase();
        if(filterText.length > 0) {
            return marketCoins.data.filter(token => token.name.toLowerCase().indexOf(filterText) > -1 || token.symbol.toLowerCase().indexOf(filterText) > -1)
        } else {
            return marketCoins.data
        }
    }, [marketCoins, query])

    useEffect(() => {
        dispatch(fetchMarketCoins());
    }, [dispatch])

    useEffect(() => {
        dispatch(fetchAllCoins(page, pageSize));
    }, [page, pageSize, dispatch])

    const paginationOptions = {
        custom: true,
        totalSize: allTokens.total,
        sizePerPageList: [
            { text: "10", value: 10 },
            { text: "20", value: 20 },
            { text: "50", value: 50 },
            { text: "100", value: 100 },
            { text: "250", value: 250 },
        ],
        sizePerPage: pageSize,
        page: page,
    };

    const changeTableHandler = (type, data) => {
        if(data.page) {
            setPage(data.page);
        }
        if(data.sizePerPage) {
            setPageSize(data.sizePerPage);
        }
    }

    const columns = (hasPagination, hasCoinFetch = false) => [
        {
            dataField: "id",
            text: "ID",
            formatter: (cellContent, row, rowIndex) => hasPagination ? ((page - 1) * pageSize) + rowIndex + 1 : rowIndex + 1,
        },
        {
            dataField: "asset",
            text: "Asset",
            formatter: (cellContent, row, rowIndex) => (
                <div key={rowIndex} className="d-flex align-items-center flex-row py-3">
                    {row.image ? (<Logo src={row.image} alt={row.name}/>) : <LogoContainer><CurrencyLogo currency={row.currency}/></LogoContainer>}
                    <div className="d-flex flex-column justify-content-center ml-3 mr-auto">
                        <CustomTitle className={'font-weight-bolder mb-1'}>{row.name}</CustomTitle>
                        <SymbolText>{row.symbol.toUpperCase()}</SymbolText>
                    </div>
                    {hasCoinFetch && (<button className="btn btn-light-primary btn-sm ml-2 d-none d-lg-block">Aggregations <ArrowDown size={18} fill={'currentColor'} /></button>)}

                </div>
            ),
        },
        {
            dataField: "price",
            text: "Price",
            formatter: (cellContent, row) => (<CellText><CurrencyText>{row.current_price}</CurrencyText></CellText>),
        },
        {
            dataField: "change_percentage_24h",
            text: "24h",
            formatter: (cellContent, row) => (
                <span className={`label px-3 px-lg-2 label-inline label-lg ${row.price_change_percentage_24h >= 0 ? 'label-light-success' : 'label-light-danger'} `}>
                    {row.price_change_percentage_24h && row.price_change_percentage_24h.toFixed(4)}%
                </span>),
        },
        {
            dataField: "change_percentage_7d",
            text: "7 days",
            formatter: (cellContent, row) => (
                <span className={`label px-3 px-lg-2 label-inline label-lg ${row.price_change_percentage_7d_in_currency >= 0 ? 'label-light-success' : 'label-light-danger'} `}>
                    {row.price_change_percentage_7d_in_currency && row.price_change_percentage_7d_in_currency.toFixed(4)}%
                </span>),
        },
        {
            dataField: "change_percentage_30d",
            text: "1 Month",
            formatter: (cellContent, row) => (
                <span className={`label px-3 px-lg-2 label-inline label-lg ${row.price_change_percentage_30d_in_currency >= 0 ? 'label-light-success' : 'label-light-danger'} `}>
                    {row.price_change_percentage_30d_in_currency && row.price_change_percentage_30d_in_currency.toFixed(4)}%
                </span>),
        },
        {
            dataField: "change_percentage_1y",
            text: "A Year",
            formatter: (cellContent, row) => (
                <span className={`label px-3 px-lg-2 label-inline label-lg ${row.price_change_percentage_1y_in_currency >= 0 ? 'label-light-success' : 'label-light-danger'} `}>
                    {row.price_change_percentage_1y_in_currency && row.price_change_percentage_1y_in_currency.toFixed(4)}%
                </span>),
        },
        {
            dataField: "market_cap",
            text: "Market Cap",
            formatter: (cellContent, row) => (
                <CellText>
                    <CurrencyText>
                        {row.market_cap}
                    </CurrencyText>
                </CellText>
            ),
        },
    ]

    const rowEvents = {
        onClick: (e, row) => {
            if(e.target.tagName === 'BUTTON') {
                if(expanded.includes(row.id)) {
                    setExpanded(expanded.filter(exRow => exRow !== row.id))
                } else {
                    dispatch(fetchCoinMarketPrices({
                        id: row.id,
                        symbol: row.symbol,
                    }))
                    setExpanded(expanded.concat(row.id));
                }
            } else {
                props.history.push(`/tools/market/${row.id}`);
            }
        }
    }

    const expandRow = {
        renderer: (row) => {
            const loading = marketCoins.prices.loading[row.id];
            const coinPrices = marketCoins.prices.data[row.id];
            return loading ? (

                <div className="d-flex flex-column flex-lg-row align-items-center justify-content-center py-5">
                    <Loading width={40} height={40} active id={'load-markets'} />
                </div>
            ) : (
                <div className="d-flex flex-column flex-lg-row align-items-lg-center py-4 px-6">
                    {(coinPrices && coinPrices.hasOwnProperty('links')) ? Object.keys(coinPrices.links).map(market => {
                        return (
                            <div className="d-flex flex-row justify-content-lg-center justify-content-start flex-grow-1 mb-3 mb-lg-0 flex-lg-column" key={market}>
                                <MarketLink className="text-white mb-lg-1 mr-2 mr-lg-0 font-size-base" href={coinPrices.links[market]} target={"_blank"} rel={'noopener noreferrer'}>{market} ↗</MarketLink>
                                <span className={`${coinPrices.result[market] >= row.current_price ? 'text-success' : 'text-danger'} font-weight-bold font-size-h4`} style={{ flex: '1'}}>
                                    <CurrencyText>
                                        {coinPrices.result[market]}
                                    </CurrencyText>
                                </span>
                            </div>
                        )
                    }) : (
                        <div className="d-flex flex-column flex-lg-row align-items-center justify-content-center py-5">
                            <CellText>Something went wrong on loading data from api</CellText>
                        </div>
                    )}
                </div>
            )
        },
        expanded: expanded
    }

    return (
        <Tab.Container defaultActiveKey="featured">
            <Row>
                <HeaderCol xs={12} className={'d-flex flex-column-reverse flex-lg-row align-items-stretch align-items-lg-start justify-content-start justify-content-lg-between'}>
                    <CustomNav fill variant="pills" className={'d-flex flex-row align-items-center flex-nowrap'} >
                        <CustomNavItem>
                            <CustomNavLink eventKey="featured">Featured Coins</CustomNavLink>
                        </CustomNavItem>
                        <CustomNavItem>
                            <CustomNavLink eventKey="all">All Coins</CustomNavLink>
                        </CustomNavItem>
                    </CustomNav>

                    <CustomInputGroup className={'w-auto mb-lg-0'}>
                        <InputGroup.Prepend>
                            <InputGroup.Text>
                                <img src={SearchIcon} alt="search"/>
                            </InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl id="PoolsSearch" placeholder="Search" onChange={e => setQuery(e.target.value.toLowerCase())}/>
                    </CustomInputGroup>
                </HeaderCol>

                <Col xs={12}>
                    <Tab.Content className={'bg-transparent'}>
                        <Tab.Pane eventKey="featured">
                            <BootstrapTable
                                wrapperClasses="table-responsive d-none d-lg-block"
                                bordered={false}
                                classes={`table table-head-custom table-borderless table-vertical-center overflow-hidden table-hover explore__table`}
                                bootstrap4
                                remote
                                keyField="id"
                                columns={columns(false, true)}
                                data={marketCoinsData}
                                rowEvents={rowEvents}
                                expandRow={expandRow}
                            >
                            </BootstrapTable>

                            <ResponsiveTable centered size={'lg'} breakpoint={'lg'} columns={columns(false, true)} data={marketCoinsData} direction={'rtl'}/>
                        </Tab.Pane>
                        <Tab.Pane eventKey="all">

                            <PaginationProvider pagination={paginationFactory(paginationOptions)}>
                                {({ paginationProps, paginationTableProps }) => {
                                    return (
                                        <BootstrapTable
                                            wrapperClasses="table-responsive d-none d-lg-block"
                                            bordered={false}
                                            classes={`table table-head-custom table-borderless table-vertical-center overflow-hidden table-hover explore__table`}
                                            bootstrap4
                                            remote
                                            keyField="id"
                                            columns={columns(true)}
                                            data={allTokensData}
                                            rowEvents={rowEvents}
                                            onTableChange={changeTableHandler}
                                            {...paginationTableProps}
                                        >
                                        </BootstrapTable>
                                    );
                                }}
                            </PaginationProvider>

                            <ResponsiveTable centered size={'lg'} breakpoint={'lg'} columns={columns(true)} data={allTokensData} direction={'rtl'}/>
                        </Tab.Pane>
                    </Tab.Content>
                </Col>
            </Row>
        </Tab.Container>
    )
}

export default withRouter(MarketTokens);
